<template>
	<div class="emb-register-wrap section-gap">
		<div class="container py-0">
			<v-layout row wrap align-center justify-center>
				<v-flex sm12 md12 lg8 xl7>
					<v-layout row mx-sm-0 mx-3  wrap align-center justify-center>
						
						<v-flex sm10 md5 lg6 xl6>
							<div class="emb-card sign-in-form form-margin d-block white pa-6">
								<h4>Enter Your Departments details</h4>
								<v-form ref="form" v-model="valid">
									<v-text-field
										type="text"
										placeholder="English Name*"
										v-model="Departments.en"
										:rules="inputRules.basictextRules"
									>
									</v-text-field>
									<v-text-field
										type="text"
										v-model="Departments.kiny"
										placeholder="kinyarwanda Name*"
										:rules="inputRules.basictextRules"
									>
									</v-text-field>
                                    <v-text-field
										type="text"
										v-model="Departments.fr"
										placeholder="french Name*"
										:rules="inputRules.basictextRules"
									>
									</v-text-field>
                                    <v-text-field
										type="text"
										v-model="Departments.sw"
										placeholder="swahili Name*"
										:rules="inputRules.basictextRules"
									>
									</v-text-field>
									<v-select :items="selectable" label="offers services"
									 v-model="offersServices" dense></v-select>
									<v-select :items="selectable" label="offers rental"
									 v-model="offersRental" dense></v-select>
									<v-btn class="accent mx-0 mb-4 mt-4" large  @click.stop.prevent="saveDetails">
										create Department
									</v-btn>
								</v-form>
							</div>
						</v-flex>
					</v-layout>
				</v-flex>
			</v-layout>
		</div>
   </div>
</template>
	
<script>
import department from "Api/department";
	export default{
   	data () {
      	return {
			  offersServices: '',
			  offersRental: '',
			  selectable: ["true","false"],
               Departments: {
                   en: '',
                   fr: '',
                   kiny: '',
                   sw: '',
                   },
         	valid: false,
          	inputRules: {
			   basictextRules: [v => !!v || 'This field should not be empty'],
            }
         }
      },
      methods: {
         saveDetails(){
                this.$refs.form.validate();
                this.createDepartments();
			// 	if(this.valid == true){
			// 		this.$router.push('/');
            // }	
			},
		 async createDepartments () {
           try {
               const res = await department
               .createDepartment({
				   name: this.Departments,
				   offersServices: this.offersServices,
				   offersRental: this.offersRental
               })
               this.$snotify.success('Add departments process succesfull',{
                    closeOnClick: false,
                    pauseOnHover: false,
                    timeout: 20,
					showProgressBar:false,
					});
               console.log(res)
               console.log('hello')
               this.$router.push('/admin-panel/Departments')
			} catch (err) {
				console.log(err)
                this.$router.push('/')
			}
		}
	}
	}
</script>